@font-face {
  font-family: 'Pretendard-Regular';
  src: url('https://cdn.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}

* {
  font-family: 'Pretendard-Regular';
}

.App {
  text-align: center;

}


.list {
  margin-top: 30px;
  text-align: left;
  padding-left: 20px;
  padding-right: 20px;
}

.guestinput {
  padding: 10px;
  width: 85%;
}

.btadmit {
  padding: 10px;
  width: auto;
}



.upbtn {
  top: 90%;
  left: 95%;
  width: 60px;
  height: 60px;
  border-radius: 80%;
  background-color: #ffffff;
  position: fixed;
  border: none;
  box-shadow: 0px 0px 10px #eee;
  transition: all 0.3s; 
}

.upbtn:hover {
  background-color: #f5f5f5;
  box-shadow: 0px 0px 10px rgb(206, 206, 206);
  transition: all 0.3s;
}


.modal {
  margin-top: 20px;
  padding: 20px;
  background-color: #eee;
  text-align: left;
}


